"use client";

import { useState } from "react";
import styles from "./CreateEvent.module.css";
import OrganizerDetails from "./steps/OrganizerDetails";
import EventDetails from "./steps/EventDetails";
import MediaTickets from "./steps/MediaTickets";

export default function CreateEvent() {
  const [step, setStep] = useState(1);

  return (
    <div className={styles.wrapper}>
      <div className={styles.stepper}>
        <span className={step === 1 ? styles.active : ""}>Organization</span>
        <span className={step === 2 ? styles.active : ""}>Event</span>
        <span className={step === 3 ? styles.active : ""}>Media</span>
      </div>

      {step === 1 && <OrganizerDetails onNext={() => setStep(2)} />}
      {step === 2 && (
        <EventDetails
          onBack={() => setStep(1)}
          onNext={() => setStep(3)}
        />
      )}
      {step === 3 && <MediaTickets onBack={() => setStep(2)} />}
    </div>
  );
}
